.profile__description {
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    margin: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 300px;
}

@media screen and (max-width: 768px) {
    .profile__description {
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        max-width: 250px;
        margin-bottom: 36px;
    }
}